import { Component, OnInit } from '@angular/core';
import { ICartela, INumerosCartela } from 'app/shared/models/interface.model';

@Component({
  selector: 'app-jogo',
  templateUrl: './jogo.component.html',
  styleUrls: ['./jogo.component.scss'],
})
export class JogoComponent implements OnInit {
  numerosCartela: INumerosCartela[] = [];
  numerosSelecionados: number[] = [];

  cartelas: ICartela[] = [];

  constructor() {}

  ngOnInit(): void {
    this.gerarNumerosAleatorios();
  }

  gerarNumerosCartela(): void {
    this.numerosCartela = [];
    for (let i = 1; i < 26; i++) {
      let numeroCartela: INumerosCartela = {
        numero: i,
        selecionado: false,
      };
      numeroCartela.selecionado = this.numerosSelecionados.includes(i);
      this.numerosCartela.push(numeroCartela);
    }

    this.cartelas.push({
      cartela: this.numerosCartela,
      selecionados: this.numerosSelecionados,
    });
  }

  gerarNumerosAleatorios(): void {
    this.cartelas = [];
    let numbers: number[] = [];

    while (numbers.length < 15) {
      let random = Math.floor(Math.random() * 25) + 1;

      if (!numbers.includes(random)) {
        numbers.push(random);
      }
    }
    this.numerosSelecionados = numbers;
    this.gerarNumerosCartela();
  }

  testarJogo(cartela: ICartela) {
    alert(`N√£o implementado ${JSON.stringify(cartela.selecionados)}`);
  }
}
