import { Component, OnInit } from '@angular/core';
import { IResultado } from 'app/shared/models/interface.model';

@Component({
  selector: 'app-resultado',
  templateUrl: './resultado.component.html',
  styleUrls: ['./resultado.component.scss'],
})
export class ResultadoComponent implements OnInit {
  concurso = 'latest';

  resultados = [];

  resultado: IResultado = {} as IResultado;

  constructor() {}

  ngOnInit(): void {}

  buscarOutroResultado(concurso: string): void {
    this.concurso = concurso;
  }

  gerarListaConcursos(resultado: IResultado): void {
    this.resultado = resultado;

    if (this.resultados.length > 0) return;

    let lista = [];

    for (let i = resultado.concurso; i >= 1; i--) {
      lista.push(i);
    }

    this.resultados = lista;
  }
}
