import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { Route, RouterModule } from '@angular/router';
import { SharedModule } from 'app/shared/shared.module';
import { JogoComponent } from './components/jogo/jogo.component';
import { DicasComponent } from './containers/dicas/dicas.component';
import { FerramentasComponent } from './containers/ferramentas/ferramentas.component';
import { GeradorComponent } from './containers/gerador/gerador.component';
import { ResultadoComponent } from './containers/resultado/resultado.component';
import { TabelasComponent } from './containers/tabelas/tabelas.component';

const routes: Route[] = [
  { path: '', redirectTo: 'resultado', pathMatch: 'full' },
  { path: 'resultado', component: ResultadoComponent },
  {
    path: 'gerador',
    children: [
      { path: '', component: GeradorComponent },
      { path: 'aleatorio', component: JogoComponent },
    ],
  },

  { path: 'tabelas', component: TabelasComponent },
  { path: 'dicas', component: DicasComponent },
  { path: 'ferramentas', component: FerramentasComponent },
];

@NgModule({
  declarations: [
    DicasComponent,
    FerramentasComponent,
    TabelasComponent,
    GeradorComponent,
    ResultadoComponent,
    JogoComponent,
  ],
  imports: [CommonModule, RouterModule.forChild(routes), SharedModule],
})
export class LotofacilModule {}
